body {
    margin: 0;
    padding: 0;
    font-family: Arial, sans-serif;
    background-color: #f0f0f0;
  }
  
  .App {
    text-align: center;
  }
  
  .chatbox {
    margin: 20px auto;
    padding: 10px;
    width: 400px;
    background-color: #ffffff;
    border: 1px solid #ccc;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  .message {
    padding: 8px;
    margin-bottom: 10px;
  }
  
  .message.user {
    background-color: #d4f1f4;
    text-align: left;
  }
  
  .message.assistant {
    background-color: #f7d4d4;
    text-align: left;
  }
  
  .chat-input {
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
  }
  
  .chat-input input {
    flex-grow: 1;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  .chat-input button {
    margin-left: 8px;
    padding: 8px 12px;
    border: none;
    background-color: #007bff;
    color: white;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .chat-input button:hover {
    background-color: #0056b3;
  }
  